/**
 * 
 */
let idCard = {
  /**
   * 
   */
  idCardValidate: function(idCard) {
    idCard = idCard ? idCard.replace(/ /g, '') : idCard; // 
    /**
     * 15
     *
     * @param idCard15
     *            15
     * @return
     */
    let isValidityBrithBy15IdCard = function(idCard15) {
      let year = idCard15.substring(6, 8);
      let month = idCard15.substring(8, 10);
      let day = idCard15.substring(10, 12);
      let temp_date = new Date(year, parseFloat(month) - 1, parseFloat(day));
      // getYear()
      if (
        temp_date.getYear() !== parseFloat(year) ||
        temp_date.getMonth() !== parseFloat(month) - 1 ||
        temp_date.getDate() !== parseFloat(day)
      ) {
        return false;
      }
      return true;
    };
    /**
     * 18
     *
     * @param {String} idCard18
     *            18
     * @return {Boolean}
     */
    let isValidityBrithBy18IdCard = function(idCard18) {
      let year = idCard18.substring(6, 10);
      let month = idCard18.substring(10, 12);
      let day = idCard18.substring(12, 14);
      let temp_date = new Date(year, parseFloat(month) - 1, parseFloat(day));
      // getFullYear()
      if (
        temp_date.getFullYear() !== parseFloat(year) ||
        temp_date.getMonth() !== parseFloat(month) - 1 ||
        temp_date.getDate() !== parseFloat(day)
      ) {
        return false;
      }
      return true;
    };
    /**
     * 18
     *
     * @param {Array} a_idCard
     *            
     * @return {Boolean}
     */
    let isTrueValidateCodeBy18IdCard = function(a_idCard) {
      let Wi = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2, 1]; // 
      let ValideCode = [1, 0, 10, 9, 8, 7, 6, 5, 4, 3, 2]; // .10X
      let sum = 0; // 
      if (a_idCard[17].toLowerCase() === 'x') {
        a_idCard[17] = 10; // x10
      }
      for (let i = 0; i < 17; i++) {
        sum += Wi[i] * a_idCard[i]; // 
      }
      valCodePosition = sum % 11; // 
      if (String(a_idCard[17]) === String(ValideCode[valCodePosition])) {
        return true;
      }
      return false;
    };
    if (/(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/.test(idCard)) {
      if (idCard.length === 15) {
        return isValidityBrithBy15IdCard(idCard); // 15
      } else if (idCard.length === 18) {
        let a_idCard = idCard.split(''); // 
        if (
          isValidityBrithBy18IdCard(idCard) &&
          isTrueValidateCodeBy18IdCard(a_idCard)
        ) {
          // 1818
          return true;
        }
        return false;
      }
    } else {
      return false;
    }
  },
  /**
   * 
   *
   * @param {String} idCard
   *            15/18
   * @return {Boolean}
   */
  birthDayByIdCard: function(idCard) {
    idCard = idCard ? idCard.replace(/ /g, '') : idCard; // 
    if (!this.idCardValidate(idCard)) return null;
    let year, month, day;
    if (idCard.length === 15) {
      year = idCard.substring(6, 8);
      month = idCard.substring(8, 10);
      day = idCard.substring(10, 12);
    } else {
      year = idCard.substring(6, 10);
      month = idCard.substring(10, 12);
      day = idCard.substring(12, 14);
    }
    return year + '-' + month + '-' + day;
  },
  /**
   * 
   *
   * @param {String} idCard
   *            15/18
   * @return {String} '0'-,'1'-
   */
  maleOrFemalByIdCard: function(idCard) {
    idCard = idCard ? idCard.replace(/ /g, '') : idCard; // 
    if (!this.idCardValidate(idCard)) return null;
    if (idCard.length === 15) {
      if (idCard.substring(14, 15) % 2 === 0) {
        return '1';
      }
      return '0';
    } else if (idCard.length === 18) {
      if (idCard.substring(14, 17) % 2 === 0) {
        return '1';
      }
      return '0';
    }
    return null;
  }
};
let rules = {
  email: {
    test: /.+@.+\..+/,
    message: ''
  },
  mobile: {
    test: /^(?:1[3456789]\d{9})?$/,
    message: ''
  },
  required: {
    test: function(value) {
      return value !== null && value !== undefined && /\S+/.test(value);
    },
    required: true,
    message: ''
  },
  //+
  englishmixing: {
    test: /^[a-zA-Z0-9]{1,}$/,
    placeholder: '',
    message: '{0}+'
  },
  url: {
    test: /^(?:\w+:)?\/\/([^\s\.]+\.\S{2}|localhost[:?\d]*)\S*$/,
    message: 'URL '
  },
  tel: {
    //
    test: /^((\d{7,8})|(\d{4}|\d{3})-(\d{7,8})|(\d{4}|\d{3})-(\d{7,8})-(\d{4}|\d{3}|\d{2}|\d{1})|(\d{7,8})-(\d{4}|\d{3}|\d{2}|\d{1}))$/,
    message: ''
  },
  fax: {
    //
    test: /^[+]{0,1}(\d){1,3}[ ]?([-]?((\d)|[ ]){1,12})+$/,
    message: ''
  },
  /**
   * 
   * 1
   * 2\(
   * 3
   * 42
   */
  fullname: {
    test: function(value) {
      return value ?
        /^[\u2E80-\u9FFF]+[|\s|\u2E80-\u9FFF]*[\u2E80-\u9FFF]+$/.test(
          value
        ) || /^[a-zA-Z]+[|\s|a-zA-Z]*[a-zA-Z|]+$/.test(value) :
        false;
    },
    message: ''
  },
  number: {
    //
    test: /^(\+|-)?\d+($|\.\d+$)/,
    message: ''
  },
  integer: {
    //()
    test: /^[0-9]+[0-9]*]*$/,
    message: ''
  },
  integerNum: {
    //()
    test: /^(([0-9]*)|(([0]\.\d{1,}|[1-9][0-9]*\.\d{1,})))$/,
    message: ''
  },
  positiveInt: {
    //
    test: /^[1-9][0-9]*$/,
    message: ''
  },
  positiveNum: {
    //+
    test: /^(([1-9][0-9]*)|(([0]\.\d{1,}|[1-9][0-9]*\.\d{1,})))$/,
    message: ''
  },
  address: {
    //510
    test: function(value) {
      function getBytesCount(value) {
        if (
          !value ||
          value === undefined ||
          value === '' ||
          value.length === 0
        ) {
          return 0;
        }
        return value.length + value.replace(/[\u0000-\u00ff]/g, '').length;
      }
      return (
        /[\u4E00-\u9FA5]/g.test(value) &&
        !/(\S+)\1{4}/gi.test(value) &&
        getBytesCount(value) >= 10
      );
    },
    message:
      '5510'
  },
  qq: {
    //QQ
    test: /[1-9]([0-9]{4,11})/,
    message: 'QQ'
  },
  wechat: {
    //
    test: /^[a-zA-Z\d_]{5,}$/,
    message: ''
  },
  username: {
    test: /^[a-zA-z][a-zA-Z0-9_-]{3,15}$/,
    placeholder: '',
    message: '{0}4-16()'
  },
  password: {
    //6-20 +
    test: /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,20}$/,
    message: '6-20'
  },
  height: {
    test: /^[1-9]\d{1,2}(\.\d{1,2})?$/,
    message: ''
  },
  weight: {
    test: /^[1-9]\d{0,2}(\.\d{1,2})?$/,
    message: ''
  },
  zipNo: {
    test: /^[1-9][0-9]{5}$/,
    message: ''
  },
  bankNo: {
    //1619
    // test: /^[1-9](\d{15}|\d{18})$/,

    //2310
    test: /^[1-9]\d{9,22}$/,
    message: '(10-23)'
  },
  date: {
    test: /^(\d{4})\-(\d{2})\-(\d{2})$/,
    placeholder: '',
    message: '{0}'
  },
  idcard: {
    test: function(value) {
      return idCard.idCardValidate(value);
    },
    placeholder: '', //
    message: '{0}'
  },
  passport: {
    //
    test: /^[A-Za-z0-9]{3,20}$/,
    placeholder: '', //
    message: '{0}3-20'
  },
  passportCN: {
    //
    test: /^[A-Za-z0-9]{7,10}$/,
    placeholder: '', //
    message: '{0}7-10'
  },
  birthCertificate: {
    test: /^[a-zA-Z]{1}[0-9]{7,11}$/,
    placeholder: '', //
    message: '{0}'
  },
  HMMainlandPass: {
    // - 11
    test: /^[a-zA-Z][0-9]{10}$/,
    placeholder: '', //
    message: '{0}'
  },
  TWMainlandPass: {
    // - 8-10
    test: /^[a-zA-Z0-9]{8,10}$/,
    placeholder: '', //
    message: '{0}'
  },
  officersCertificate: {
    test: /^[a-zA-Z0-9]{10,18}$/,
    placeholder: '', //
    message: '{0}10-18'
  },
  soldierCard: {
    test: /^[a-zA-Z0-9]{10,18}$/,
    placeholder: '', //
    message: '{0}10-18'
  },
  taxpayerCode: {
    test: /^([A-Z0-9]{15}|[A-Z0-9]{18}|[A-Z0-9]{20})$/,
    placeholder: '', //
    message: '{0}151820'
  },
  platenumber: {
    test: /^[A-Z]{1}[A-Z]{1}[]{0,1}[A-Z0-9]{4}[A-Z0-9]{1}[DF1-9]{0,1}$/,
    placeholder: '', //
    message: '{0}'
  },
  minlength: {
    //
    test: function(value, rule) {
      //rule 
      return value && rule && value.length >= parseInt(rule);
    },
    message: '{0}'
  },
  maxlength: {
    //
    test: function(value, rule) {
      return value && rule && value.length <= parseInt(rule);
    },
    message: '{0}'
  },
  min: {
    //
    test: function(value, rule) {
      return value && rule && Number(value) >= Number(rule || '0');
    },
    message: '{0}'
  },
  max: {
    //
    test: function(value, rule) {
      return (
        value &&
        rule &&
        Number(value) <= Number(rule || Number.POSITIVE_INFINITY)
      );
    },
    message: '{0}'
  },
  base: {
    //
    test: function(value, rule) {
      return value && value % parseInt(rule || '1') === 0;
    },
    message: '{0}'
  }
};
module.exports = {
  rules: rules,
  idCard: idCard
};



